#repeat-menu-desktop{
    top: 0px;
    /* To center it */
    left: 30%;
    background-color: rgba(20,20,20,.65);
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
    padding-left: 10px;
    padding-right: 25px;
    position: absolute;
    align-items: center;
    flex-flow: wrap;
    align-content: space-between;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    color: white;
    cursor: grab;
    z-index: 1;
    width: 400px;
}

.trellis-control-desktop{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-left: 10px;
    > *{
        margin: 2px;
    }
}

#repeat-menu-mobile{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    pointer-events: none;
    justify-content: center;
    display: flex;
    flex-direction: column;
}

#repeat-right{
    left: 0px;
}

#repeat-left{
    right: 0px;
}

.repeat-side{
    position: absolute;
    background-color: rgba(20,20,20,.65);
    border-radius: 10px;
    align-content: space-between;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    color: white;
    cursor: grab;
    z-index: 1;
    padding: 2px;
    pointer-events: all;
}

